@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  width: 100%;
  background-image: url(/movies/static/media/movie.b24a0e09.jpg);

  background-size: 100%, 100%;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  margin: 1em, auto;
  padding-top: 2em;
}
.container h1 {
  width: 100%;
  height: 2em;
  color: white;
  background-color: red;
}
.grid {
  width: 900px;
  height: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 0.4em;
  justify-content: center;
}
.movie {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(20, 189, 201);
  margin-bottom: 20px;
  padding-bottom: 10px;
}
.movie button {
  width: 80%;
  height: 3em;
  background-color: black;
  color: white;
  border-radius: 10px;
  margin-top: 4px;
}
.movie button:hover {
  background-color: bisque;
  color: tomato;
  cursor: pointer;
  border: bisque;
}
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 2.3em;
  background-color: rgb(0, 0, 0);
  padding-left: 2em;
  padding-right: 2em;
}
.search input {
  height: 2em;
  border-radius: 5px;
  padding: 2px;
  margin-right: 3px;
}
.search button {
  height: 2em;
  border-radius: 5px;
  background-color: black;
  color: white;
  border: black;
}
.search button:hover {
  cursor: pointer;
  background-color: bisque;
  color: black;
}
.logo {
  font-size: larger;
  color: white;
}
.logo:hover {
  cursor: pointer;
  color: red;
}

@media screen and (max-width: 400px) {
  header {
    display: block;
    height: 4em;
  }
  .search {
    width: 100%;
    display: flex;
    justify-content: end;
  }
}

.Movie_movie__3INCW {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(20, 189, 201);
  border-radius: 5px;
}
.Movie_movie__3INCW img {
  width: 100%;
  padding: 0.5em;
}
@media screen and (max-width: 480px) {
  .Movie_movie__3INCW img {
    padding: 0.1em;
  }
}

